.gallery {
    background-color: #fff;
    padding-top: 60px;
    padding-bottom: 60px;

    &__mainTitle {
        font-family: 'Yeseva One';
font-weight: 400;
font-size: 40px;
line-height: 50px;

color: #222A46;
margin-bottom: 30px;
    }
    &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }

    // .gallery__item

    &__item {
        img{
    border: 1px solid #E2ECE8;
    border-radius: 10px;
        }
    }
}
.arrows {
    position: absolute;
    top: 50%;
    z-index: 99;
    padding: 15px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow: 0px 4px 15px rgba(34, 42, 70, 0.15);
    display: flex;
    justify-content: center;
    align-items: center;
    &.arrowLeft {
        left: -20px;
    }
    
    &.arrowRight {
        right: -20px;
    }
}



